var that
export default {
	data() {
		return {
			
		};
	},
	onLoad(){
		that = this
	},
	methods:{
		closeLogin(){
			this.$store.commit('setShowLogin',false)
		},
		getuserinfo: function (e) {
		  console.log('getuserinfo')
		  this.wxLogin()
		},
		wxLogin(){
			let that = this
			uni.showLoading({
				title:''
			})
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				console.log(loginRes)
				that.$post(that,{
				  url:'getMiniToken',
				  data:{'code':loginRes.code},
				  success(data){
					// that.userValue(data)
						let miniToken = data.miniToken
						uni.getUserInfo({
					      provider: 'wxLogin',
					      success: function (infoRes) {
					        console.log(infoRes);
							that.$post(that,{
							  url:'wxLogin',
							  data:{
								  'type':2,
								  'encryptedData':infoRes.encryptedData,
								  'iv':infoRes.iv,
								  'miniToken':miniToken
							   },
							  success(data){
								  console.log(data)
								  uni.setStorage({
								      key: 'token',
								      data: data.token,
								      success: function () {
								          that.loginSuccess(data)
								      }
								  })
								  
								// that.userValue(data)
							  },
							  error(res){
								// alert('error'+JSON.stringify(res))
							  }
							})
					      }
					    });
				  },
				  error(res){
					// alert('error'+JSON.stringify(res))
				  }
				})
			  }
			});
		},
		loginSuccess(data){
			this.$store.commit('setUserData',data)
			this.$store.commit('setLogin',true)
			this.$store.commit('setShowLogin',false)
		}
	}
}
